@import "../commons/default";
@import "../fonts/scss/fonts-en";
body {
  font-size: 14px;
  font-family: $primary-font;
  background-color: #f6f6f6;
  color: $text;
}
.btn.btn-secondary.dropdown-toggle {
  background-color: transparent !important;
  border: none;
  @include box-shadow-none;
  padding: 0;
  text-align: left;
  &:after {
    display: none;
  }
}
button {
  cursor: pointer;
  @include box-shadow-none;
}
sub {
  font-family: $gel;
  bottom: 0px;
  font-size: 12px;
}

@media (max-width: 1799px) and (min-width: 1500px) {
  .container {
    max-width: 1400px;
  }
}

@media (min-width: 1800px) {
  .container {
    max-width: 1662px;
  }
}

.bg__site {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 3;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  &.active {
    opacity: 1;
    visibility: visible;
  }
}
.search-nav {
  background-color: #fff;
  width: 100%;
  height: 60px;
  z-index: 4;
  margin-top: 10px;
  padding: 0 10px;
  opacity: 0;
  visibility: hidden;
  transition: 0.4s;
  position: absolute;
  &_block {
    height: 60px;
    input {
      height: 100%;
      width: 90%;
      @include font-style($primary-font, 16px, $dark-gray);
      padding-left: 10px;
      &::placeholder {
        color: $dark-gray;
      }
    }
    .delete {
      border: none;
      background-color: initial;
      .fa-times {
        color: #c1c1c1;
      }
    }
    .search-btn {
      margin-left: 15px;
      background-color: #fa7268;
      color: #fff;
      font-size: 16px;
      width: 40px;
      height: 40px;
      border: none;
      @include border-radius(10px);
      box-shadow: 0 2px 3px #e2e1e1;
      outline: none;
    }
  }
  &.active {
    opacity: 1;
    visibility: visible;
  }
}
.burger-nav {
  position: absolute;
  top: 0;
  z-index: 4;
  height: 100vh;
  width: 85%;
  padding: 25px 20px;
  background-color: #fff;
  left: -100%;
  transition: 0.4s;
  &.active {
    left: 0;
  }
  .burger-close {
    position: absolute;
    top: 30px;
    right: -35px;
    color: #fff;
    opacity: 1;
    background-color: initial;
    border: none;
  }
  .burger-nav_block {
    overflow-y: auto;
    height: 100%;
  }
  .menu-item {
    margin-bottom: 20px;
    .burgermenu-title {
      .title {
        @include font-style($bold-font, 18px, $dark-gray);
      }
      i {
        color: #afaaac;
        font-size: 12px;
      }
    }
    .menu-item_inner {
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.15s ease-out;
      margin-top: 5px;
      margin-left: 10px;
      .link {
        display: block;
        @include font-style($primary-font, 15px, $grey);
        margin-bottom: 10px;
      }
    }
    .fa-chevron-up {
      display: none;
    }
    &.active {
      .fa-chevron-up {
        display: block;
      }
      .fa-chevron-down {
        display: none;
      }
      .menu-item_inner {
        max-height: 500px;
        transition: max-height 0.25s ease-in;
      }
    }
  }
  .burger-sub_link {
    @include font-style($primary-font, 18px, $dark-gray);
    margin-bottom: 15px;
    display: block;
  }
}
.user-nav {
  position: absolute;
  top: 0;
  z-index: 4;
  height: 100vh;
  width: 85%;
  padding: 25px 0;
  background-color: #fff;
  left: -100%;
  transition: 0.4s;
  &.active {
    left: 0;
  }
  .burger-close {
    position: absolute;
    top: 30px;
    right: -35px;
    color: #fff;
    opacity: 1;
    background-color: initial;
    border: none;
  }
  .user-image {
    padding-left: 30px;
    margin-bottom: 20px;
    img {
      width: 50px;
      height: 50px;
    }
    .email {
      margin-left: 15px;
      @include font-style($primary-font, 14px, $dark-gray);
    }
  }
  .profile-menu {
    margin-top: 20px;
  }
  .profile-left {
    overflow-y: auto;
    height: 100%;
  }
}

.modal-backdrop.show {
  opacity: 0.7;
}
.modal-lg {
  @media (min-width: 768px) {
    max-width: 680px;
  }
  @media (min-width: 1200px) {
    max-width: 820px;
  }
  @media (min-width: 1500px) {
    max-width: 1160px;
  }
  @media (max-width: 767px) {
    width: 100%;
    max-width: 100%;
    height: 100%;
    margin: 0;
  }
}
.modal.login {
  .close {
    position: absolute;
    right: -40px;
    top: -30px;
    opacity: 1;
    @media (max-width: 767px) {
      z-index: 2;
      top: 10px;
      right: 10px;
    }
  }
  .modal-content {
    height: 750px;
    background-color: transparent;
    border: none;
    margin: 0 -15px;
    .modal-content_block {
      height: 100%;
      @include border-radius(8px);
      overflow: hidden;
      .login_content {
        padding: 0;
      }
      .bg {
        height: 100%;
        background-image: url("../../images/flower.png");
        background-size: cover;
      }
      .logForm {
        height: 100%;
        background-color: #fff;
        opacity: 0;
        visibility: hidden;
        transition: 0.3s;
        &-item {
          position: relative;
          width: 315px;
          height: 55px;
          border: 1px solid #c1c1c1;
          @include border-radius(8px);
          margin-bottom: 20px;
          &_icon {
            width: 55px;
            border-right: 1px solid #c1c1c1;
          }
          &_input {
            height: 95%;
            margin: auto;
            border: none;
            padding-left: 20px;
            width: calc(100% - 65px);
            @include font-style($bold-font, 16px, #c1c1c1);
            &::placeholder {
              /* Chrome, Firefox, Opera, Safari 10.1+ */
              color: #c1c1c1;
              opacity: 1; /* Firefox */
            }
            &:-ms-input-placeholder {
              /* Internet Explorer 10-11 */
              color: #c1c1c1;
            }
            &::-ms-input-placeholder {
              /* Microsoft Edge */
              color: #c1c1c1;
            }
          }
          .show-pass {
            border: none;
            position: absolute;
            right: 20px;
            top: 15px;
            cursor: pointer;
          }
        }
        .btn-block {
          width: 315px;
          .remember {
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            cursor: pointer;
            position: relative;
            padding-left: 35px;
            @include font-style($primary-font, 14px, #c1c1c1);
            .hide {
              position: absolute;
              opacity: 0;
              height: 0;
              width: 0;
            }
            .checkmark {
              position: absolute;
              top: 0;
              left: 0;
              height: 21px;
              width: 21px;
              border: 2px solid #c1c1c1;
              @include border-radius(4px);
              &:after {
                content: "";
                left: 6px;
                top: 2px;
                width: 5px;
                height: 9px;
                border: solid $text;
                border-width: 0 2px 2px 0;
                -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                transform: rotate(45deg);
                position: absolute;
                display: none;
              }
            }
          }
          .remember input:checked ~ .checkmark:after {
            display: block;
          }
          .btn {
            background-color: $dark-gray;
            border: 1px solid $dark-gray;
            @include font-style($primary-font, 16px, #fff);
            @include border-radius(8px);
            padding: 13px 13px 14px;
            &:hover {
              background-color: #fff;
              color: $dark-gray;
            }
          }
        }
        .registration-btn {
          @include font-style($bold-font, 16px, $red);
          line-height: 19px;
          margin-top: 40px;
          &:hover {
            text-shadow: 0.5px 0 $red;
          }
        }
        .or {
          width: 315px;
          margin: 20px 0;
          @include font-style($bold-font, 16px, #c1c1c1);
          span {
            width: 40%;
            height: 1px;
            background-color: #eaeaea;
          }
        }
        .loginBtn {
          width: 315px;
          height: 55px;
          overflow: hidden;
          @include font-style($primary-font, 14px, #fff);
          @include border-radius(8px);
          margin-bottom: 20px;
          .icon {
            width: 55px;
            height: 100%;
          }
          .txt {
            width: calc(100% - 57px);
          }
          &.fb {
            background-color: #3b5998;
            border: 1px solid #3b5998;
            .icon {
              background-color: #263c68;
              i {
                font-size: 22px;
              }
            }
            &:hover {
              background-color: #fff;
              color: #3b5998;
              i {
                color: #fff;
              }
            }
          }
          &.google {
            border: 1px solid #f44336;
            background-color: #f44336;
            .icon {
              background-color: #c53126;
              i {
                font-size: 16px;
              }
            }
            &:hover {
              background-color: #fff;
              color: #f44336;
              i {
                color: #fff;
              }
            }
          }
        }
        .pass-forgot {
          @include font-style($primary-font, 14px, $dark-gray);
          margin-top: 80px;
          &:hover {
            text-shadow: 1px 0 $dark-gray;
          }
        }
        &.reg-wrapper {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: 1;
          .logForm-item:nth-child(3) {
            margin-top: 20px;
          }
          .or-reg {
            position: absolute;
            left: -40px;
            top: 0;
            height: 130px;
            @include font-style($bold-font, 15px, #c1c1c1);
            span {
              width: 1px;
              height: 35px;
              background-color: #eaeaea;
            }
          }
        }
        &.active {
          opacity: 1;
          visibility: visible;
        }
      }
    }
    @media (max-width: 1499px) {
      height: 600px;
      .modal-content_block {
        .logForm {
          .logForm-item_input,
          .btn-block .btn,
          .registration-btn {
            font-size: 14px;
          }
          .logForm-item,
          .btn-block,
          .loginBtn {
            width: 290px;
            height: 50px;
          }
          .pass-forgot {
            margin-top: 30px;
          }
        }
      }
    }
    @media (max-width: 991px) {
      height: 550px;
    }
    @media (max-width: 767px) {
      width: 100%;
      height: 100%;
      margin: 0;
      .modal-content_block {
        @include border-radius(0);
        .or-reg {
          left: -25px !important;
        }
      }
    }
  }
}
.burger-btn {
  width: 20px;
  height: 17px;
  span {
    height: 3px;
    width: 20px;
    background-color: $dark-gray;
    display: block;
    &:nth-child(2) {
      margin: 4px 0;
    }
  }
}
.error {
  border: 1px solid $red !important;
  margin-bottom: 40px !important;
  .error-txt {
    width: 100%;
    position: absolute;
    bottom: -25px;
    color: $red;
  }
}
.site__header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  z-index: 2;
  .header {
    &-sale {
      background-color: $red;
      padding: 13px 0;
      height: 50px;
      &_txt {
        @include font-style($primary-font, 16px, #fff);
      }
      @media (max-width: 1499px) {
        padding: 10px 0;
        height: 40px;
        &_txt {
          font-size: 14px;
          @media (max-width: 1199px) {
            font-size: 12px;
          }
          @media (max-width: 767px) {
            font-size: 10px;
          }
        }
      }
      @media (max-width: 767px) {
        padding: 8px 0;
        height: 30px;
      }
    }
    .sup-hdr {
      background-color: #fff;
      border-bottom: 1px solid #eaeaea;
      padding: 15px 0;
      height: 55px;
      li {
        margin-right: 30px;
        position: relative;
        &.store {
          &:after {
            content: "";
            position: absolute;
            width: 1px;
            height: 34px;
            background-color: #eaeaea;
            top: -5px;
            right: -15px;
          }
        }
      }
      &_link {
        @include font-style($primary-font, 14px, $text);
        img {
          margin-right: 15px;
        }
        &:hover {
          color: $red;
          text-shadow: 0.5px 0 #fa7268;
        }
        @media (max-width: 1499px) {
          font-size: 12px;
        }
        @media (max-width: 1199px) {
          font-size: 10px;
          img {
            margin-right: 5px;
            width: 14px;
          }
        }
        &.login-user {
          //@include display-flex;
          //@include align-items(center);
          //@include justify-content(flex-end);
          img {
            width: 40px;
            height: 40px;
          }
          .name {
            margin-left: 5px;
            @include font-style($primary-font, 14px, $dark-gray);
          }
        }
      }
      .lang {
        @include font-style($bold-font, 14px, $text);
        position: absolute;
        right: -40px;
        top: 50%;
        transform: translateY(-50%);
        &:before {
          content: "";
          position: absolute;
          width: 1px;
          height: 34px;
          background-color: #eaeaea;
          top: -5px;
          left: -15px;
        }
        @media (max-width: 1250px) {
          position: relative;
          right: 0;
          margin-left: 28px;
        }
      }
      @media (max-width: 1499px) {
        padding: 12px 0;
      }
      @media (max-width: 1199px) {
        height: 45px;
      }
    }
    .sub-hdr {
      background-color: #fff;
      padding: 28px 0;
      height: 105px;
      .logo {
        display: block;
        @media (max-width: 1499px) {
          img {
            width: 200px;
          }
        }
        @media (max-width: 1199px) {
          img {
            width: 150px;
          }
        }
      }
      .search {
        background-color: #ebebeb;
        @include border-radius(10px);
        padding: 5px 6px 5px 0;
        &-dropdown {
          width: 250px;
          border-right: 1px solid #d8d8d8;
          .btn {
            background-color: transparent;
            border: none;
            @include font-style($bold-font, 16px, $text);
            @include box-shadow-none;
            padding: 0;
            width: 100%;
            height: 100%;
            text-align: left;
            padding-left: 30px;
            &:after {
              display: none;
            }
            img {
              margin-right: 20px;
            }
            i {
              margin-left: 10px;
            }
          }
          .dropdown-menu {
            width: 100%;
            border: none;
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
            margin-top: 10px;
            border-radius: 10px;
            .dropdown-item {
              @include font-style($primary-font, 18px, #707070);
              padding: 10px 40px 10px 30px;
              @media (max-width: 1499px) {
                font-size: 12px;
              }
              @media (max-width: 1199px) {
                padding: 8px 0 8px 15px;
              }
            }
          }
          .fa-angle-up {
            display: none;
          }
          &.show {
            .fa-angle-down {
              display: none;
            }
            .fa-angle-up {
              display: inline-block;
            }
          }
        }
        &-input {
          background-color: transparent;
          border: none;
          width: calc(100% - 270px);
          margin-left: 30px;
          @include font-style($primary-font, 16px, #bebdbd);
          &::placeholder {
            /* Chrome, Firefox, Opera, Safari 10.1+ */
            color: #bebdbd;
            opacity: 1; /* Firefox */
          }
          &:-ms-input-placeholder {
            /* Internet Explorer 10-11 */
            color: #bebdbd;
          }
          &::-ms-input-placeholder {
            /* Microsoft Edge */
            color: #bebdbd;
          }
        }
        &-btn {
          @include border-radius(10px);
          background-color: $red;
          border: 1px solid $red;
          color: #fff;
          width: 40px;
          height: 40px;
          cursor: pointer;
          &:hover {
            color: $red;
            background-color: #fff;
          }
        }
        @media (max-width: 1499px) {
          margin-right: 25px;
          &-dropdown {
            width: 200px;
            .btn {
              padding-left: 15px;
              font-size: 14px;
              img {
                margin-right: 10px;
              }
            }
          }
          &-input {
            width: calc(100% - 290px);
            margin-left: 10px;
            font-size: 14px;
          }
        }
        @media (max-width: 1199px) {
          margin-right: 25px;
          height: 40px;
          &-dropdown {
            width: 170px;
            .btn {
              padding-left: 15px;
              font-size: 12px;
              img {
                margin-right: 5px;
                width: 18px;
              }
            }
          }
          &-input {
            width: calc(100% - 230px);
            margin-left: 10px;
            font-size: 12px;
          }
          &-btn {
            width: 30px;
            height: 30px;
          }
        }
        @media (max-width: 991px) {
          margin-right: 0;
          &-dropdown {
            width: 150px;
            .btn {
              padding-left: 8px;
              img {
                width: 16px;
              }
              i {
                margin-left: 5px;
              }
            }
          }
          &-input {
            width: calc(100% - 200px);
          }
        }
      }

      .hdr-fav {
        margin-left: 30px;
        img {
          margin-right: 0 !important;
          @media (max-width: 1199px) {
            width: 30px !important;
          }
          @media (max-width: 991px) {
            width: 22px !important;
          }
        }
        .dropdown-menu {
          margin-left: 0;
          //margin-top: 30px;
        }
        @media (max-width: 1699px) {
          margin-left: 10px;
        }
      }
      @media (max-width: 1499px) {
        padding: 14px 0;
        height: 80px;
      }
      @media (max-width: 1199px) {
        height: 70px;
      }
    }
    .header-menu {
      height: 80px;
      &_item {
        padding: 0 10px;
        .link {
          @include font-style($primary-font, 14px, $dark-gray);
          padding: 30px 0;
          display: block;
          @media (max-width: 1499px) {
            font-size: 12px;
          }
        }
        .inner-menu {
          position: absolute;
          width: 100%;
          max-height: 500px;
          background-color: #fff;
          left: 0;
          top: 100%;
          opacity: 0;
          visibility: hidden;
          transition: 0.3s;
          padding-top: 90px;
          .inner-menu_block {
            width: 220px;
            margin-right: 30px;
          }
          .title {
            @include font-style($primary-font, 22px, $dark-gray);
            margin-bottom: 25px;
            text-shadow: 1px 0;
          }
          .categories {
            .items {
              overflow: scroll;
              max-height: 350px;
              overflow-y: scroll;
              overflow-x: hidden;
              position: relative;
              &-link {
                @include font-style($primary-font, 18px, $dark-gray);
                margin-bottom: 8px;
                display: block;
                &:hover {
                  text-shadow: 1px 0;
                }
              }
              .letter {
                @include font-style($bold-font, 18px, $dark-gray);
                margin: 15px 0 5px;
              }
              .letter-link {
                display: block;
                @include font-style($primary-font, 14px, $dark-gray);
              }
              &::-webkit-scrollbar {
                width: 5px;
              }
              /* Track */
              &::-webkit-scrollbar-track {
                background: #fff;
              }
              /* Handle */
              &::-webkit-scrollbar-thumb {
                background: #ebebeb;
              }
            }
          }
          .suggestion {
            .image {
              @include border-radius(10px);
              overflow: hidden;
              img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
              }
              &:hover {
                img {
                  transition: 0.5s;
                  transform: scale(1.1);
                  transform-origin: 50% 50%;
                }
              }
            }
            .prod-name {
              margin-top: 20px;
              @include font-style($primary-font, 18px, #afaaac);
            }
            .price {
              .old-price {
                margin-right: 20px;
                @include font-style($primary-font, 18px, #afaaac);
                span {
                  text-decoration: line-through;
                }
              }
              .new-price {
                @include font-style($primary-font, 18px, $dark-gray);
              }
            }
            &.left {
              margin-right: 160px;
            }
          }
          .brands-list {
            .list {
              width: 500px;
              &-item {
                @include font-style($primary-font, 18px, $dark-gray);
                margin-right: 55px;
                margin-bottom: 30px;
                width: 12px;
                height: 30px;
                display: inline-block;
                &:last-child {
                  font-family: $bold-font;
                }
                &:hover {
                  color: $red;
                  text-decoration: underline;
                }
                @media (max-width: 1799px) {
                  margin-right: 50px;
                  margin-bottom: 20px;
                }
              }
              @media (max-width: 1199px) {
                width: 350px;
                &-item {
                  margin-right: 30px;
                  margin-bottom: 10px;
                }
              }
            }
          }
          @media (max-width: 1499px) {
            padding-top: 50px;
            .title {
              font-size: 16px;
            }
            .items-link {
              font-size: 14px !important;
            }
            .left {
              margin-right: 80px !important;
            }
            .suggestion {
              .prod-name {
                font-size: 14px;
              }
            }
          }
          @media (max-width: 1199px) {
            max-height: 300px;
            padding-top: 20px;
            .categories {
