.heart {
  position: relative;
  .added {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    opacity: 0;
    visibility: hidden;
    transition: 0.4s;
    width: 26px;
    height: 23px;
  }
  &.active {
    .added {
      opacity: 1;
      visibility: visible;
      -webkit-animation: heart 0.2s infinite; /* Safari 4.0 - 8.0 */
      animation: heart 0.2s infinite;
      animation-iteration-count: 1;
    }
  }
}
.cart {
  position: relative;
  .qty {
    position: absolute;
    right: -9px;
    top: -8px;
    width: 16px;
    height: 16px;
    background-color: $red;
    @include border-radius(50%);
    @include font-style($primary-font, 9px, #fff);
    @include display-flex;
    @include align-items(center);
    @include justify-content(center);
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
  }
  &.active {
    .qty {
      opacity: 1;
      visibility: visible;
    }
  }
}
/* Safari 4.0 - 8.0 */
@-webkit-keyframes heart {
  0% {
    width: 18px;
    height: 16px;
  }
  100% {
    width: 22px;
    height: 20px;
  }
}

/* Standard syntax */
@keyframes heart {
  0% {
    width: 18px;
    height: 16px;
  }
  100% {
    width: 22px;
    height: 20px;
  }
}

