      .swiper-slide {
        height: 100%;
        @include display-flex;
        @include align-items(center);
        @include justify-content(center);
        img {
          max-width: 90%;
          opacity: 0.3;
          transition: 0.3s;
          max-height: 100%;
        }
        &:hover {
          img {
            opacity: 1;
          }
        }
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      background-image: initial;
      top: 25px;
    }
    .swiper-button-prev {
      left: 47%;
      img {
        transform: rotate(180deg);
      }
    }
    .swiper-button-next {
      right: 47%;
    }
    @media (max-width: 767px) {
      padding-top: 50px;
      .swiper-button-prev {
        left: 45%;
      }
      .swiper-button-next {
        right: 45%;
      }
    }
    @media (max-width: 575px) {
      .swiper-button-prev {
        left: 42%;
        width: 12px;
      }
      .swiper-button-next {
        right: 42%;
        width: 12px;
      }
    }
  }
  @media (max-width: 575px) {
    padding: 40px 0;
  }
}
.comments {
