}
.map-content {
  position: relative;
  .map-btn {
    background-color: $red;
    border: 1px solid $red;
    @include font-style($primary-font, 18px, #fff);
    padding: 12px 55px;
    @include border-radius(10px);
    position: relative;
    display: block;
    z-index: 1;
    margin: auto;
    margin-bottom: -20px;
    &:after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 10px solid $red;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
    }
    @media (max-width: 1499px) {
      font-size: 14px;
    }
    @media (max-width: 991px) {
      margin-bottom: -10px;
