    transition: 0.4s;
    display: none;
    #map {
      height: 460px;
      @media (max-width: 575px) {
        height: 300px;
      }
    }
    &.active {
      height: 460px;
      width: 100%;
      display: block;
      @media (max-width: 575px) {
        height: 300px;
      }
    }
  }
}
.swiper-slide {
  .image {
    .img {
      img {
        max-width: 90%;
      }
    }
  }
}
.news-titleBlock_link {
  @media (max-width: 1499px) {
    font-size: 12px;
  }
}
