.filter-block {
  width: 280px;
  .burger-close {
    position: absolute;
    top: 30px;
    right: -35px;
    color: #fff;
    opacity: 1;
    background-color: initial;
    border: none;
  }
  .filter {
    max-height: 40px;
    overflow: hidden;
    margin-bottom: 40px;
    .filter-title {
      cursor: pointer;
      position: relative;
      @include font-style($bold-font, 18px, $dark-gray);
      .toggle-btn {
        i {
          color: $grey;
          font-size: 12px;
        }
        .fa-angle-down {
          display: none;
        }
      }
    }
    .filter-menu {
      margin-top: 20px;
      max-height: 180px;
      overflow: hidden;
      .filter-link {
        cursor: pointer;
        padding-left: 30px;
        margin-bottom: 10px;
        position: relative;
        @include font-style($primary-font, 18px, $grey);
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        input {
          position: absolute;
          opacity: 0;
          cursor: pointer;
        }
        .checkmark {
          position: absolute;
          top: 4px;
          left: 0;
          height: 18px;
          width: 18px;
          border: 2px solid #c1c1c1;
          @include border-radius(4px);
          background-color: #fff;
          &:after {
            content: "";
            position: absolute;
            display: none;
            left: 5px;
            top: 3px;
            width: 8px;
            height: 11px;
            border: solid white;
            border-width: 0 3px 3px 0;
            -webkit-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            transform: rotate(45deg);
          }
        }
        &.color {
          width: 30px;
          height: 30px;
          @include border-radius(50%);
          margin-right: 15px;
          .checkmark {
            background-color: initial;
            border: none;
            width: 30px;
            height: 30px;
            top: 0;
          }
          input:checked ~ .checkmark {
            background-color: initial;
          }
          input:checked ~ .checkmark:after {
            left: 10px;
            top: 6px;
            width: 9px;
            height: 13px;
          }
        }
      }
      .filter-link input:checked ~ .checkmark {
        background-color: $dark-gray;
        border: none;
      }
      .filter-link input:checked ~ .checkmark:after {
        display: block;
      }
      &.active {
        max-height: 100%;
      }
    }
    .more-filter {
      margin-top: 20px;
      border: none;
      background-color: transparent;
      @include font-style($primary-font, 16px, #c1c1c1);
      &:hover {
        color: $grey;
      }
    }
    &.active {
      max-height: 100%;
      .filter-title {
        .toggle-btn {
          .fa-angle-down {
            display: block;
          }
          .fa-angle-right {
            display: none;
          }
        }
      }
    }
    &.price-filter {
      overflow: initial;
      .price-range {
        position: relative;
        .money_range {
          padding: 0;
          margin-bottom: 30px;
          @include display-flex;
          @include flex-direction(column-reverse);
          width: 100%;
          .input_blocks {
            .inps_bl {
              width: 85px;
              margin-right: 10px;
              input {
                border: 1px solid #c1c1c1;
                @include border-radius(4px);
                width: 100%;
                height: 38px;
                text-align: center;
                @include font-style($bold-font, 18px, $dark-gray);
              }
            }
            .ok-btn {
              border: none;
              background-color: initial;
              @include font-style($bold-font, 18px, $dark-gray);
            }
          }
          #slider-range {
            clear: left;
            float: left;
            width: 100%;
            max-width: 100%;
            margin-top: 20px;
            margin-bottom: 30px;
            height: 3px;
            background: #c1c1c1;
            border: 1px solid #c1c1c1;
            @include border-radius(20px);
            .ui-slider-range {
              background: $dark-gray;
              height: 3px;
            }
            span.ui-slider-handle {
              width: 30px;
              height: 30px;
              border: 3px solid $dark-gray;
              top: -14px;
              background-color: #fff;
              @include border-radius(50%);
              &:after {
                content: "\f111";
                font-family: FontAwesome;
                position: absolute;
                display: block;
                color: $dark-gray;
                font-size: 17px;
                top: 50%;
                left: 50%;
                transform: translate3d(-50%, -50%, 0);
              }
            }
          }
        }
      }
    }
  }
  @media (max-width: 991px) {
    position: absolute;
    top: 0;
    left: -100%;
    height: 100vh;
    width: 320px;
    z-index: 4;
    background-color: #fff;
    transition: 0.4s;
    .list {
      padding: 25px 20px;
      overflow-y: auto;
      height: 100vh;
    }
    .cat-banner {
      display: none;
    }
    #slider-range {
      width: 90% !important;
    }
    &.active {
      left: 0;
    }
  }
}
