      }
    }
  }
  .filter-btn {
    border: 2px solid #f7b3ae;
    @include font-style($primary-font, 10px, $dark-gray);
    @include border-radius(5px);
    width: 150px;
    height: 40px;
    background-color: transparent;
  }
  @media (max-width: 991px) {
    margin-bottom: 20px;
  }
}
table {
  .progress-td {
    width: 360px;
    .progress {
      background-color: #ebebeb;
      width: 100%;
      height: 10px;
      @include border-radius(0);
      overflow: initial;
      .progress-bar {
        position: relative;
        background-color: #ebebeb;
        .status {
          @include font-style($primary-font, 14px, $grey);
          position: absolute;
          font-size: 14px;
          margin-top: 25px;
          left: 55%;
          display: none;
          width: 105px;
          text-align: center;
        }
        .deliv-date {
          @include font-style($primary-font, 14px, $grey);
          background-color: #fff;
          position: absolute;
          top: 20px;
          left: 10%;
          width: auto;
          min-width: 180px;
          white-space: nowrap;
          border: 4px solid #ebebeb;
          @include border-radius(10px);
          padding: 17px 19px 15px;
          &:before {
            content: "";
            position: absolute;
            bottom: 100%;
            left: 50%;
            transform: translateX(-50%);
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-bottom: 10px solid #ebebeb;
          }
          @media (max-width: 1499px) {
            left: -15%;
          }
          @media (max-width: 1199px) {
            left: -50%;
          }
        }
        &:after {
          content: "";
          position: absolute;
          right: 2px;
          width: 4px;
          height: 4px;
          border-radius: 50% !important;
          background-image: url("../../images/ellipse-red.svg");
          background-repeat: no-repeat;
          background-size: contain;
        }
        &.active {
          background-color: $red;
          &:after {
            content: "";
            position: absolute;
            right: -8px;
            width: 15px;
            height: 15px;
            border: 2px solid #fff;
            @include border-radius(50%);
            background-color: $red;
            box-shadow: 0 2px 5px #ec9c96;
            z-index: 1;
          }
          .status {
            display: block;
          }
        }
        &.completed {
          &:after {
            content: "";
            right: 0;
            width: 4px;
            height: 4px;
            @include border-radius(50%);
            background-image: url("../../images/ellipse-w.svg");
            background-repeat: no-repeat;
            background-size: contain;
            border: none;
          }
          .status {
            display: none;
          }
        }
      }
    }
    @media (max-width: 1499px) {
      width: 280px;
    }
    @media (max-width: 1199px) {
      width: 220px;
    }
  }
  .order-code {
    @include font-style($primary-font, 20px, $dark-gray);
    position: relative;
    &_hover {
      border: 4px solid #ebebeb;
      @include border-radius(10px);
      padding: 15px;
      position: absolute;
      left: -70px;
      top: 40px;
      width: max-content;
      background-color: #fff;
      opacity: 0;
      visibility: hidden;
      transition: 0.2s;
      z-index: 1;
      .image {
        width: 80px !important;
        height: 80px !important;
        margin-right: 10px !important;
        img {
          max-width: 80%;
        }
      }
      .title {
        width: 140px;
        @include font-style($primary-font, 12px, $grey);
      }
      &:before {
        content: "";
        position: absolute;
        bottom: 100%;
        left: 30%;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-bottom: 10px solid #ebebeb;
      }
    }
    &:hover {
      .order-code_hover {
        opacity: 1;
        visibility: visible;
      }
    }
    @media (max-width: 1499px) {
      font-size: 16px;
    }
    @media (max-width: 1199px) {
      font-size: 14px;
    }
  }
  .vote {
    @include font-style($primary-font, 16px, #fff);
    background-color: $red;
    border: 2px solid $red;
    height: 53px;
    padding: 14px 10px;
    @include border-radius(10px);
    &:hover {
      background-color: #fff;
      color: $red;
    }
    @media (max-width: 1499px) {
      font-size: 12px;
      padding: 10px 5px;
      height: 41px;
    }
    @media (max-width: 991px) {
      font-size: 10px;
    }
  }
  .complain {
    margin-left: 10px;
    @include font-style($primary-font, 16px, $grey);
    background-color: #fff;
    border: 4px solid #ebebeb;
    padding: 14px 10px;
    @include border-radius(10px);
    @media (max-width: 1499px) {
      font-size: 12px;
      padding: 10px 5px;
    }
    @media (max-width: 1199px) {
      margin-left: 5px;
    }
    @media (max-width: 991px) {
      margin-left: 3px;
      font-size: 10px;
    }
  }
}
.order-t {
  overflow: visible;
}
.site__footer {
  background-color: $dark-gray;
  padding-top: 80px;
  .footer {
    .title {
      @include font-style($primary-font, 22px, $grey);
      text-shadow: 1px 0;
      margin-bottom: 25px;
    }
    .footer-link {
      @include font-style($primary-font, 18px, $grey);
      display: block;
      margin-bottom: 8px;
      img {
        margin-right: 15px;
      }
      &:hover {
        color: #f6f6f6;
      }
    }
    .contact-block {
      .footer-link {
        margin-bottom: 20px;
      }
    }
    .sup-footer {
      margin-bottom: 70px;
      .card-block {
        &_item {
          margin-right: 15px;
          .soc {
            background-color: #f6f6f6;
            width: 34px;
            height: 34px;
            @include border-radius(50%);
            color: $dark-gray;
            font-size: 18px;
            &:hover {
              border: 1px solid #f6f6f6;
              background-color: transparent;
              color: #f6f6f6;
            }
          }
        }
      }
      form {
        background-color: #c5c4c4;
        @include border-radius(30px);
        height: 60px;
        position: relative;
        input {
          border: none;
          background-color: transparent;
          width: 350px;
          height: 100%;
          padding-left: 35px;
          border-right: 1px solid #b6b5b5;
          @include font-style($primary-font, 16px, #706d6e);
          padding-right: 20px;
        }
        label {
          position: absolute;
          top: -30px;
          left: 20px;
          color: #fff;
        }
        .sent {
          border: none;
          background-color: transparent;
          padding: 0 15px;
          @include font-style($primary-font, 16px, $dark-gray);
          text-shadow: 1px 0;
        }
      }
      .lang {
        @include font-style($bold-font, 14px, #c5c4c4);
        letter-spacing: 0.2em;
        margin-left: 50px;
        &:hover {
          color: #f6f6f6;
        }
      }
    }
    .follow {
      @media (max-width: 767px) {
        margin: 25px 0 30px;
      }
    }
    .pay .title,
    .follow .title {
      letter-spacing: 2px;
    }
    @media (max-width: 1499px) {
      .title {
        font-size: 16px;
        margin-bottom: 15px;
      }
      .footer-link {
        font-size: 14px;
      }
      .sup-footer {
        margin-bottom: 30px;
        form {
          height: 40px;
          input,
          .sent {
            font-size: 14px;
          }
        }
      }
    }
    @media (max-width: 1199px) {
      .title {
        font-size: 14px;
      }
      .card-block_item {
        margin-right: 10px;
        img {
          width: 32px;
        }
      }
      .footer-link,
      input,
      .sent {
        font-size: 12px !important;
      }
      input {
        width: 280px !important;
      }
    }
  }
  .rights {
    margin-top: 30px;
    border-top: 1px solid #4b494a;
    padding: 18px 0;
    p {
      @include font-style($primary-font, 14px, $grey);
    }
    @media (max-width: 1499px) {
      margin-top: 15px;
      padding: 10px 0;
      p {
        font-size: 12px;
      }
    }
  }
  @media (max-width: 991px) {
    padding-top: 20px;
  }
}

@media (max-width: 767px) {
  .orders-history,
  .orders {
    min-height: calc(100vh - 600px);
    display: none;
    &.active {
      display: block;
    }
    .profile-top {
      display: none;
    }
  }
}

.hdr-cart {
  .btn {
    @include display-flex;
    img {
