        font-size: 16px;
      }
      .title {
        font-size: 14px;
      }
    }
    @media (max-width: 1199px) {
      .price {
        font-size: 14px;
      }
      .title {
        font-size: 12px;
      }
    }
  }
  .dropdown-menu {
    border: none;
    width: 100%;
